.roomBlock {
  margin-top: 15px;
  margin-left: calc(5% - 2px);
}

.roomBlockContainer {
  overflow: hidden;
}

.itemsList {
  text-align: left;
  padding-left: 10%;
  overflow: scroll;
  display: flex;
  flex-direction: column;
  background-color: oldlace;
  overflow: scroll;
}

.itemsList > h3 {
  margin-bottom: 1px;
}
.itemsList > p {
  margin-top: 1px;
  margin-bottom: 1px;
  padding-left: 5%;
}

.newItemButton {
  margin-top: 5%;
  margin-bottom: 5%;
  flex: 1;
}

.room {
  display: flex;
  flex-direction: column;
  height: 85vh;
}

.roomDisplay {
  flex: 5;
}